import React from 'react';

// MUI Components
import TextField from '@mui/material/TextField';
import Box from '@mui/system/Box';
import Button from '@mui/material/Button';
import {
  GridToolbarColumnsButton,
  GridToolbarDensitySelector,
  GridToolbarFilterButton,
  GridToolbarExport
} from '@mui/x-data-grid';

// MUI Styling
import { createTheme } from '@mui/material/styles';
import { createStyles, makeStyles } from '@mui/styles';

// react-icons
import { FaCog, FaSearch, FaTrash, FaUserPlus } from 'react-icons/fa';


const defaultTheme = createTheme();

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      root: {
        padding: theme.spacing(0.5, 0.5, 0),
        justifyContent: 'space-between',
        display: 'flex',
        alignItems: 'flex-start',
        flexWrap: 'wrap',
      }
    }),
  { defaultTheme },
);

const Toolbar = (props) => {

  const [settingsShow, setSettingsShow] = React.useState(false);
  const toggleSettings = () => setSettingsShow(!settingsShow);
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Box sx={{ margin: 1, display: 'flex' }}>
        <Button size="medium" sx={{ color: "#000", '&:hover': { color: "#00BFA5" } }} onClick={toggleSettings}>
          <FaCog style={{ marginRight: 5 }} /> Settings
        </Button>
        {settingsShow &&
          (<div style={{ marginTop: 2 }}>
            <GridToolbarColumnsButton style={{ color: "#000" }} />
            <GridToolbarFilterButton style={{ color: "#000" }} />
            <GridToolbarDensitySelector style={{ color: "#000" }} />
          </div>)
        }
        <GridToolbarExport style={{ color: "#000" }} />
        <Button size="medium" sx={{ color: "#000", '&:hover': { color: "#00BFA5" } }} onClick={() => props.create()}>
          <FaUserPlus style={{ marginRight: 5 }} />
          {props.createTitle ?? "Create"}
        </Button>
        <Button disabled={props.isMultipleDelete} size="medium" sx={{ color: "#000", '&:hover': { color: "#E45570" } }} onClick={() => props.delete()}>
          <FaTrash style={{ marginRight: 5 }} />
          Delete
        </Button>
      </Box>
      <Box sx={{ m: 0 }}>
        <TextField 
          type="search" 
          variant="outlined"
          size="small"
          onChange={props.onChange} 
          placeholder="Search"
          InputProps={{
            startAdornment: <FaSearch style={{ marginRight: 5 }} />,
          }}
          sx={{
            padding: 1
          }}
        />
      </Box>

    </div>
  );
}

export default Toolbar;