import React from 'react';

// MUI Component
import { GridOverlay } from '@mui/x-data-grid';
import LinearProgress from '@mui/material/LinearProgress';

export default function GridLoadingOverlay() {
    return (
        <GridOverlay>
            <div style={{
                position: 'absolute',
                paddingLeft: 0,
                paddingRight: 0,
                top: 0,
                width: '100%',
            }}>
                <LinearProgress />
            </div>
        </GridOverlay>
    )
}
