import React from 'react'
import utilityService from '../../../services/utils/utilityServices';

// Redux
import { useDispatch } from 'react-redux';
import { post, put } from '../../../redux/async/rolesAsync';

// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import RolesForm from '../forms/RolesForm';

export default function RolesModal(props) {

    const title = utilityService.capitalizeFirstLetter(props.action);
    const dispatch = useDispatch();

    const handleSubmit = (params) => {
        if (props.action === "create") {
            dispatch(post(params));
        } else {            
            dispatch(put(params));
        }

    }

    return (
        <div>
            <InnerModalDialog open={props.open} title={`${title} Role`} toggle={props.toggle}>
                <RolesForm action={props.action} handleSubmit={handleSubmit} />
            </InnerModalDialog>
        </div>
    )
}
