import React from 'react'

// Redux
import { useDispatch } from 'react-redux';
import { post, put } from '../../../redux/async/usersAsync';
import utilityService from '../../../services/utils/utilityServices';

// Modal Components
import UsersForm from '../forms/UsersForm';
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';

export default function Create(props) {

    const title = utilityService.capitalizeFirstLetter(props.action);
    const dispatch = useDispatch();

    const handleSubmit = (params) => {
        if(props.action === "create"){
            dispatch(post(params));
        }else{
            dispatch(put(params));
        }
        
    }

    return (
        <div>
            <InnerModalDialog open={props.open} title={`${title} User`} toggle={props.toggle}>
                <UsersForm action={props.action} handleSubmit={handleSubmit} />
            </InnerModalDialog>
        </div>
    )
}
