import React from 'react';

// Utilities 
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';

// Redux
import { useSelector, useDispatch } from 'react-redux';
import { destroy, get } from '../../../redux/async/branchesAsync';
import { selectedBranch } from '../../../redux/slices/branchSlice';

// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';

// Page Components
import GridLoadingOverlay from '../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../grid/GridNoRowOverlay';
import Toolbar from '../grid/Toolbar';
import BranchModal from '../modals/BranchModal';

// react-icons
import { FaEdit, FaTrash } from 'react-icons/fa';

const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            onChange={props.onChange}
            isMultipleDelete={props.isMultipleDelete}
            create={props.create}
            delete={props.delete}
        />
    );
}

export default function BranchesDataTable(props) {

    const dispatch = useDispatch();
    const branchesRedux = useSelector((state) => state.branches.value);
    const loading = branchesRedux.loading;
    const [rowData, setRowData] = React.useState([]);

    const [selected, setSelected] = React.useState([]);
    const [isMultipleDelete, setIsMultipleDelete] = React.useState(true);

    const [modalAction, setModalAction] = React.useState("");
    const [showBranchModal, setShowBranchModal] = React.useState(false);
    const toggleShowBranchModal = () => setShowBranchModal(!showBranchModal);

    const width = 250;
    const columns = [
        {
            field: 'branch', headerName: 'Branches', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.branch.toUpperCase()}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'manager', headerName: 'Manager', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.manager === null ? "..." : params.row.manager}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'location', headerName: 'Location', flex: 1, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.location === null ? "..." : params.row.location}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'actions', headerName: 'Options', type: 'actions', getActions: (params) => [
                <GridActionsCellItem
                    icon={<FaEdit size={18} />}
                    onClick={() => handleEdit(params)}
                    label="Edit"
                    sx={{
                        '&:hover': {
                            color: "#00BFA5"
                        }
                    }}
                    showInMenu
                />,
                <GridActionsCellItem
                    icon={<FaTrash size={15} />}
                    onClick={() => handleDelete(1, params.row.id)}
                    label="Delete"
                    sx={{
                        '&:hover': {
                            color: "#E45570"
                        }
                    }}
                    showInMenu
                />
            ]
        },
    ];

    const handleSelection = (value) => {

        setSelected(value);

        if (value.length > 0) {
            setIsMultipleDelete(false);
        } else {
            setIsMultipleDelete(true);
        }
    }

    const handleEdit = async (params) => {
        dispatch(selectedBranch(params.row));
        setModalAction("edit");
        setShowBranchModal(true);
    }

    const handleDelete = async (count = 0, row = null) => {

        let rows = selected

        if (row !== null) {
            rows = [row];
        }

        let recordCount = count > 0 ? 1 : rows.length;
        let response = await alertService.delete(recordCount);
        if (response.isConfirmed) {
            dispatch(destroy({ 'ids': rows }))
        }
    }

    const search = (text) => {
        let filtered = utilityService.filterArrayOfObjects(text, branchesRedux.branches);
        setRowData(filtered);
    };

    const loadBranches = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            loadBranches();
        }

        return () => mounted = true;
    }, [loadBranches]);

    return (
        <div style={{ height: 550, width: '100%' }}>
            {showBranchModal && <BranchModal open={showBranchModal} toggle={toggleShowBranchModal} action={modalAction} />}
            <DataGrid
                rows={rowData.length <= 0 ? branchesRedux.branches : rowData}
                columns={columns}
                selectionModel={selected}
                onSelectionModelChange={handleSelection}
                checkboxSelection={true}
                loading={loading}
                density="standard"
                disableSelectionOnClick
                disableColumnMenu
                components={{
                    LoadingOverlay: LoadingOverlayOverride,
                    NoRowsOverlay: NoRowOverlayOverride,
                    Toolbar: ToolbarOverride
                }}
                componentsProps={{
                    toolbar: {
                        create: () => { setModalAction("create"); setShowBranchModal(true); dispatch(selectedBranch({})); },
                        delete: () => { handleDelete() },
                        isMultipleDelete: isMultipleDelete,
                        onChange: (event) => search(event.target.value),
                    },
                }}
            />
        </div>
    );
}