import React from 'react'

// Redux
import { useDispatch } from 'react-redux';
import { reset } from '../../../redux/async/usersAsync';

// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import ResetPasswordForm from '../forms/ResetPasswordForm';

export default function ResetPassword(props) {

    const dispatch = useDispatch();

    const handleSubmit = (params) => {
        
        let data = {
            params: params,
            id: props.selected[0]
        }

        dispatch(reset(data));
    }

    return (
        <div>
            <InnerModalDialog open={props.open} title={"Reset Password"} toggle={props.toggle}>
                <ResetPasswordForm handleSubmit={handleSubmit} />
            </InnerModalDialog>
        </div>
    )
}
