
import React from 'react';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';

// react-icons
import { FaCheck } from 'react-icons/fa';
import { MdVisibility, MdVisibilityOff } from 'react-icons/md';



export default function UsersForm(props) {

    const usersRedux = useSelector((state) => state.users.value);
    const [password, setPassword] = React.useState("");
    const [isDefaultPassword, setIsDefaultPassword] = React.useState(false);
    const toggleIsDefault = () => setIsDefaultPassword(!isDefaultPassword)
    const [isShowPassword, setIsShowPassword] = React.useState(false)
    const toggleIsShowPassword = () => setIsShowPassword(!isShowPassword)

    const handleSubmit = async (e) => {

        e.preventDefault();

        let data = {}
        if (!isDefaultPassword) {
            data = {
                "password": password
            }
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 500, width: 500 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <FormControlLabel
                                control={<Checkbox value={isDefaultPassword} onClick={toggleIsDefault} color="primary" />}
                                label="Use System's Default Password: abc123"
                            />
                        </Grid>
                        {!isDefaultPassword &&
                            <Grid item xs={12}>
                                <TextField
                                    onChange={(e) => setPassword(e.target.value)}
                                    value={password}
                                    variant="outlined"
                                    size="small"
                                    required
                                    fullWidth
                                    name="password"
                                    label="Temporary Password"
                                    type={isShowPassword ? "text" : "password"}
                                    id="password"
                                    InputProps={{
                                        endAdornment: (
                                            <InputAdornment position="end">
                                                <IconButton onClick={toggleIsShowPassword} size="small">
                                                    {isShowPassword ? <MdVisibility /> : <MdVisibilityOff />}
                                                </IconButton>
                                            </InputAdornment>
                                        ),
                                    }}
                                />
                            </Grid>
                        }
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={usersRedux.loading}
                            >
                                Reset
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
