
import React from 'react';
import utilityServices from '../../../services/utils/utilityServices';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { FaCheck } from 'react-icons/fa';
import Manager from './fields/Manager';

export default function BranchForm(props) {

    const branchRedux = useSelector((state) => state.branches.value); 
    const [branch, setBranch] = React.useState(branchRedux.selectedBranch?.branch?.toUpperCase() ?? "");
    const [location, setLocation] = React.useState(branchRedux.selectedBranch?.location ?? "");

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "data": {
                "branch": branch,
                "manager_id": null,
                "location": location
            },
            "params": branchRedux.selectedBranch
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setBranch(e.target.value)}
                                value={branch}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="branch"
                                label="Branch"
                                name="branch"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <Manager />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setLocation(e.target.value)}
                                value={location}
                                variant="outlined"
                                size="small"
                                multiline
                                maxRows={3}
                                minRows={3}
                                required
                                fullWidth
                                id="location"
                                label="Location"
                                name="location"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={branchRedux.loading}
                            >
                                {utilityServices.capitalizeFirstLetter(props.action)}
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
