
import React from 'react';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';

// Form Components
import Branch from './fields/Branch';

// react-icons
import { FaCheck } from 'react-icons/fa'; 


export default function ChangeBranchForm(props) {

    const usersRedux = useSelector((state) => state.users.value);
    const branchRedux = useSelector((state) => state.branches.value);
    const [branch, setBranch] = React.useState(usersRedux.selectedUser?.role_id ?? "");
    const [label, setLabel] = React.useState("");


    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "branch": label,
            "branch_id": branch
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12} sx={{ minWidth: 500 }}>
                            <Branch branch={branch} setBranch={setBranch} setLabel={setLabel} />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={usersRedux.loading}
                                disabled={branchRedux.loading}
                            >
                                Change
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
