import React from 'react'

// Redux
import { useDispatch } from 'react-redux';
import { branch } from '../../../redux/async/usersAsync';

// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import ChangeBranchForm from '../forms/ChangeBranchForm';

export default function ChangeBranchModal(props) {

    const dispatch = useDispatch();

    const handleSubmit = (params) => { 
        
        let data = {
            params: params,
            index: props.selectedParams.index,
            id: props.selectedParams.id
        }

        dispatch(branch(data));
    }

    return (
        <div>
            <InnerModalDialog open={props.open} title={"Change Branch"} toggle={props.toggle}>
                <ChangeBranchForm handleSubmit={handleSubmit} />
            </InnerModalDialog>
        </div>
    )
}
