import React from 'react';

// Utilities 
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';

// Redux
import { useSelector, useDispatch } from 'react-redux';
import { get, destroy, toggle } from '../../../redux/async/usersAsync';

// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import Tooltip from '@mui/material/Tooltip';

// Page Components
import GridLoadingOverlay from '../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../grid/GridNoRowOverlay';
import Toolbar from '../grid/Toolbar';
import StatusSwitch from '../shared/StatusSwitch/StatusSwitch';
import UserModal from '../modals/UserModal';
import ResetPasswordModal from '../modals/ResetPasswordModal';
import ChangeRoleModal from '../modals/ChangeRoleModal';
import ChangeBranchModal from '../modals/ChangeBranchModal';

// react-icons
import { FaRedoAlt, FaTrash, FaUserCog, FaUsers } from 'react-icons/fa';

const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            onChange={props.onChange}
            isMultipleDelete={props.isMultipleDelete}
            create={props.create}
            delete={props.delete}
        />
    );
}

export default function UsersDataTable(props) {

    const dispatch = useDispatch();
    const usersRedux = useSelector((state) => state.users.value);
    const loading = usersRedux.loading;
    const [rowData, setRowData] = React.useState([]);

    const [selected, setSelected] = React.useState([]);
    const [selectedParams, setSelectedParams] = React.useState({});
    const [isMultipleDelete, setIsMultipleDelete] = React.useState(true);

    const [modalAction, setModalAction] = React.useState("");
    const [showUserModal, setShowModal] = React.useState(false);
    const toggleUserModal = () => setShowModal(!showUserModal);
    const [showResetPasswordModal, setShowResetPasswordModal] = React.useState(false);
    const toggleResetPasswordModal = () => setShowResetPasswordModal(!showResetPasswordModal);
    const [showChangeRoleModal, setShowChangeRoleModal] = React.useState(false);
    const toggleChangeRoleModal = () => setShowChangeRoleModal(!showChangeRoleModal);
    const [showBranchModal, setShowBranchModal] = React.useState(false);
    const toggleChangeBranchModal = () => setShowBranchModal(!showBranchModal);

    const width = 200;
    const columns = [
        { field: 'email', headerName: 'Email', flex: 1 },
        {
            field: 'role', headerName: 'Role', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {utilityService.capitalizeFirstLetter(params.row.role)}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'branch', headerName: 'Branch', width: 100, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.branch.toUpperCase()}
                        </span>
                    </div>
                );
            }
        },
        { field: 'last_login', headerName: 'Last login', type: 'date', width: width, hide: true },
        { field: 'creator', headerName: 'Created by', width: 150, hide: true },
        {
            field: 'is_active', headerName: 'Status', align: 'center', headerAlign: 'center', sortable: false, renderCell: (params) => {

                let status = params.row.is_active;

                return (
                    <Tooltip arrow={true} title={status ? "Active" : "Inactive"} placement="left">
                        <div>
                            <StatusSwitch status={status} onChange={() => dispatch(toggle({ params: params.row }))} />
                        </div>
                    </Tooltip>
                );
            }
        },
        { field: 'last_login_ip', headerName: 'IP Address', width: width, hide: true },
        { field: 'last_login_device', headerName: 'Device', width: width, hide: true },
        {
            field: 'actions', headerName: 'Options', type: 'actions', getActions: (params) => [
                <GridActionsCellItem
                    icon={<FaUserCog size={18} />}
                    onClick={() => handleChangeRole(params)}
                    label="Change Role"
                    sx={{
                        '&:hover': {
                            color: "#00BFA5"
                        }
                    }}
                    showInMenu
                />,
                <GridActionsCellItem
                    icon={<FaUsers size={15} />}
                    onClick={() => handleChangeBranch(params)}
                    label="Change Branch"
                    sx={{
                        '&:hover': {
                            color: "#00BFA5"
                        }
                    }}
                    showInMenu
                />,
                <GridActionsCellItem
                    icon={<FaRedoAlt size={15} />}
                    onClick={() => handleReset(params)}
                    label="Reset Password"
                    sx={{
                        '&:hover': {
                            color: "#00BFA5"
                        }
                    }}
                    showInMenu
                />,
                <GridActionsCellItem
                    icon={<FaTrash size={15} />}
                    onClick={() => handleDelete(1, params.row.id)}
                    label="Delete"
                    sx={{
                        '&:hover': {
                            color: "#E45570"
                        }
                    }}
                    showInMenu
                />
            ]
        },
    ];

    const handleSelection = (value) => {

        setSelected(value);

        if (value.length > 0) {
            setIsMultipleDelete(false);
        } else {
            setIsMultipleDelete(true);
        }
    }

    const handleChangeRole = async (params) => {

        setSelectedParams(params.row);

        let response = await alertService.changeRole();
        if (response.isConfirmed) {
            setShowChangeRoleModal(true);
        }
    }

    const handleChangeBranch = async (params) => {

        setSelectedParams(params.row);

        let response = await alertService.changeBranch();
        if (response.isConfirmed) {
            setShowBranchModal(true);
        }
    }

    const handleReset = async (params) => {

        setSelected([params.row.id]);

        let response = await alertService.reset({ title: "Reset account password?", text: "This account password will be reset" });
        if (response.isConfirmed) {
            setShowResetPasswordModal(true);
        }
    }

    const handleDelete = async (count = 0, row = null) => {

        let rows = selected

        if (row !== null) {
            rows = [row];
        }

        let recordCount = count > 0 ? 1 : rows.length;
        let response = await alertService.delete(recordCount);
        if (response.isConfirmed) {
            dispatch(destroy({ 'ids': rows }));
        }
    }

    const search = (text) => {
        let filteredUsers = utilityService.filterArrayOfObjects(text, usersRedux.users);
        setRowData(filteredUsers);
    };

    const loadUsers = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {
        
        let mounted = false;

        if (!mounted) {
            loadUsers();
        }

        return () => mounted = true;

    }, [loadUsers]);

    return (
        <div style={{ height: 550, width: '100%' }}>
            {showUserModal && <UserModal open={showUserModal} toggle={toggleUserModal} action={modalAction} />}
            {showResetPasswordModal && <ResetPasswordModal open={showResetPasswordModal} toggle={toggleResetPasswordModal} selected={selected} />}
            {showChangeRoleModal && <ChangeRoleModal open={showChangeRoleModal} toggle={toggleChangeRoleModal} selectedParams={selectedParams} />}
            {showBranchModal && <ChangeBranchModal open={showBranchModal} toggle={toggleChangeBranchModal} selectedParams={selectedParams} />}

            <DataGrid
                rows={rowData.length <= 0 ? usersRedux.users : rowData}
                columns={columns}
                selectionModel={selected}
                onSelectionModelChange={handleSelection}
                checkboxSelection={true}
                loading={loading}
                density="standard"
                disableSelectionOnClick
                disableColumnMenu
                components={{
                    LoadingOverlay: LoadingOverlayOverride,
                    NoRowsOverlay: NoRowOverlayOverride,
                    Toolbar: ToolbarOverride
                }}
                componentsProps={{
                    toolbar: {
                        create: () => { setModalAction("create"); setShowModal(true); },
                        delete: () => { handleDelete() },
                        isMultipleDelete: isMultipleDelete,
                        onChange: (event) => search(event.target.value),
                    },
                }}
            />
        </div>
    );
}