import * as React from 'react';

// MUI Components 
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';

// Page Components
import TabPanel from './components/TabPanel2';

// react-icons
import { FaUserLock } from 'react-icons/fa';

// options
import userstabs from './options/userstabs';

export default function UsersTabs() {

    const options = userstabs;
    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };


    return (
        <Box
            sx={{ display: 'flex', flexGrow: 1, flexDirection: 'row', height: '100vh', width: '100vw', overflow: 'hidden' }}
        >
            <Box>
                <Box display="flex" flexDirection="row" sx={{ p: 1, ml: 0, mr: -10, width: 220, backgroundColor: '#eee' }}>
                    <FaUserLock color='#00BFA5' size={25} style={{ marginRight: 5, marginTop: 2, marginLeft: 5 }} />
                    <Typography variant="h6">
                        Users
                    </Typography>
                </Box>
                <Tabs
                    orientation="vertical"
                    variant="scrollable"
                    value={value}
                    onChange={handleChange}
                    sx={{ height: '100vh', flexGrow: 1, width: 220, pt: 0, borderRight: '2px solid #eee' }}
                    TabIndicatorProps={{
                        style: {
                            right: 1,
                            height: 30,
                            marginTop: 10
                        }
                    }}
                >

                    {
                        options.map((data, index) => {
                            return <Tab onClick={() => handleChange(index)} sx={{ alignItems: "start" }} key={`tabkey-${index}`} label={
                                <Typography variant="subtitle2">
                                    <Box display="flex">
                                        {data.icon}
                                        {data.name}
                                    </Box>
                                </Typography>
                            } />
                        })
                    }
                </Tabs>
            </Box>
            <Box display="flex" flexGrow={1}>
                {
                    options.map((data, index) => {
                        return <TabPanel value={value} index={index} key={`tabpanelkey-${index}`}>
                            <Paper square elevation={0} sx={{ m: 1, height: 600, width: 'inherit' }}>
                                {data.content}
                            </Paper>
                        </TabPanel>
                    })
                }
            </Box>
        </Box>
    );
}
