
import React from 'react';
import utilityService from '../../../services/utils/utilityServices';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";

// Form Components
import Roles from './fields/Roles';
import Branch from './fields/Branch';

// react-icons
import { FaCheck } from 'react-icons/fa';
import { MdVisibility, MdVisibilityOff } from 'react-icons/md';


export default function UsersForm(props) {

    const usersRedux = useSelector((state) => state.users.value);
    const [username, setUsername] = React.useState(usersRedux.selectedUser?.username ?? "");
    const [password, setPassword] = React.useState("");
    const [email, setEmail] = React.useState(usersRedux.selectedUser?.email ?? "");
    const [branch, setBranch] = React.useState(usersRedux.selectedUser?.branch ?? "");
    const [roles, setRoles] = React.useState(usersRedux.selectedUser?.role_id ?? "");

    const [isShowPassword, setIsShowPassword] = React.useState(false)
    const toggleIsShowPassword = () => setIsShowPassword(!isShowPassword)

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "username": username,
            "password": password,
            "password_confirmation": password,
            "email": email,
            "role_id": roles,
            "branch_id": branch,
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12} sx={{ minWidth: 500 }}>
                            <Roles roles={roles} setRoles={setRoles} />
                        </Grid>
                        <Grid item xs={12} sx={{ minWidth: 500 }}>
                            <Branch branch={branch} setBranch={setBranch} />
                        </Grid>
                        {props.action === "create" &&
                            <Grid item xs={12}>
                                <TextField
                                    onChange={(e) => setEmail(e.target.value)}
                                    value={email}
                                    variant="outlined"
                                    size="small"
                                    required
                                    fullWidth
                                    id="email"
                                    label="Email Address"
                                    name="email"
                                    type="email"
                                />
                            </Grid>
                        }
                        {props.action === "create" &&
                            <Grid item xs={12}>
                                <TextField
                                    onChange={(e) => setUsername(e.target.value)}
                                    value={username}
                                    variant="outlined"
                                    size="small"
                                    required
                                    fullWidth
                                    id="username"
                                    label="Username"
                                    name="username"
                                />
                            </Grid>
                        }
                        {props.action === "create" &&
                            <Grid item xs={12}>
                                <TextField
                                    onChange={(e) => setPassword(e.target.value)}
                                    value={password}
                                    variant="outlined"
                                    size="small"
                                    required
                                    fullWidth
                                    name="password"
                                    label="Temporary Password"
                                    type={isShowPassword ? "text" : "password"}
                                    id="password"
                                    InputProps={{
                                        endAdornment: (
                                            <InputAdornment position="end">
                                                <IconButton onClick={toggleIsShowPassword} size="small">
                                                    {isShowPassword ? <MdVisibility /> : <MdVisibilityOff />}
                                                </IconButton>
                                            </InputAdornment>
                                        ),
                                    }}
                                />
                            </Grid>
                        }
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={usersRedux.loading}
                            >
                                {utilityService.capitalizeFirstLetter(props.action)}
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
