
import React from 'react';
import utilityServices from '../../../services/utils/utilityServices';
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { FaCheck } from 'react-icons/fa';

export default function RolesForm(props) {

    const rolesRedux = useSelector((state) => state.roles.value);
    const [roleName, setRoleName] = React.useState(rolesRedux.selectedRole?.role ?? "");
    const [roleDescription, setRoleDescription] = React.useState(rolesRedux.selectedRole?.description ?? "");

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "data": {
                "role": roleName,
                "description": roleDescription
            },
            "params": rolesRedux.selectedRole
        }

        props.handleSubmit(data);
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setRoleName(e.target.value)}
                                value={roleName}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="role"
                                label="Role"
                                name="role" 
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setRoleDescription(e.target.value)}
                                value={roleDescription}
                                variant="outlined"
                                size="small"
                                multiline
                                maxRows={5}
                                minRows={5}
                                required
                                fullWidth
                                id="description"
                                label="Description"
                                name="description"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                loading={rolesRedux.loading}
                            >
                                {utilityServices.capitalizeFirstLetter(props.action)}
                                <FaCheck style={{ marginLeft: 4 }} />
                            </LoadingButton>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
