import React from 'react'

// Redux
import { useDispatch } from 'react-redux';
import { change } from '../../../redux/async/usersAsync';

// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import ChangeRoleForm from '../forms/ChangeRoleForm';

export default function ChangeRoleModal(props) {

    const dispatch = useDispatch();

    const handleSubmit = (params) => {
        let data = {
            params: params,
            index: props.selectedParams.index,
            id: props.selectedParams.id
        }

        dispatch(change(data));
    }

    return (
        <div>
            <InnerModalDialog open={props.open} title={"Change Role"} toggle={props.toggle}>
                <ChangeRoleForm handleSubmit={handleSubmit} />
            </InnerModalDialog>
        </div>
    )
}
